@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Ubuntu', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.comment:hover {
  background-color: #f5f5f5;
  cursor: pointer;
}

.btton,
.btton:active,
.btton:visited,
.btton:focus {
    background: none;
    border-color: #055f83;
    border-style: solid;
    color: #055f83;
    border-radius: 30px;
}

.btton:hover{
    background-color:  #055f83;
    border-style: solid;
    border-color: #055f83;
    color: azure;
}

.btton_feed,
.btton_feed:active,
.btton_feed:visited,
.btton_feed:focus {
    background: none;
    border-color: #0baceb00;
    border-style: solid;
    color: #055f83;
    border-radius: 30px;
}

.btton_feed:hover{
    background-color:  #055f83;
    border-style: solid;
    border-color: #06688f;
    color: rgb(0, 0, 0);
}

.bttn,
.bttn:active,
.bttn:visited,
.bttn:focus {
    background: none;
    color: #055f83;
}
.bttn:hover{
    color: black;
}

.bttnx,
.bttnx:active,
.bttnx:visited,
.bttnx:focus {
    background: none;
    color: #055f83;
}

.bttnx:hover{
    color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

